import {
  InitialConfigType,
  LexicalComposer,
} from "@lexical/react/LexicalComposer";
import { ContentEditable } from "@lexical/react/LexicalContentEditable";
import { LinkPlugin } from "@lexical/react/LexicalLinkPlugin";
import { ListPlugin } from "@lexical/react/LexicalListPlugin";
import {
  CodeHighlightNode,
  CodeNode,
  registerCodeHighlighting,
} from "@lexical/code";
import { AutoLinkNode, LinkNode } from "@lexical/link";
import { ListItemNode, ListNode } from "@lexical/list";
import { $isRootTextContentEmpty } from "@lexical/text";
import { useLexicalComposerContext } from "@lexical/react/LexicalComposerContext";
import { RichTextPlugin } from "@lexical/react/LexicalRichTextPlugin";
import { HeadingNode, QuoteNode } from "@lexical/rich-text";
import { TableCellNode, TableNode, TableRowNode } from "@lexical/table";
import {
  ReactNode,
  Ref,
  RefObject,
  useEffect,
  useImperativeHandle,
} from "react";
import ErrorBoundary from "../ErrorBoundary";
import { MarkdownShortcutPlugin } from "@lexical/react/LexicalMarkdownShortcutPlugin";
import ToolbarPlugin from "./ToolbarPlugin";
import {
  $convertFromMarkdownString,
  $convertToMarkdownString,
  TRANSFORMERS,
} from "@lexical/markdown";
import { Card } from "@mantine/core";
import { css } from "@emotion/react";
import { markdownCss } from "../markdown/MarkdownRenderer";
import { useFocusWithin } from "@mantine/hooks";

function MyOnChangePlugin({
  onChange,
}: Readonly<{
  onChange?: (isEmpty: boolean) => void;
}>) {
  const [editor] = useLexicalComposerContext();
  // const debouncedChange = useDebounceCallback(onChange);
  // useEffect(() => {
  //   console.log(value);
  //   editor.getEditorState().read(() => {
  //     try {
  //       editor.setEditorState(value);
  //     } catch (err) {
  //       console.error("Failed to sync editor state:", err);
  //     }
  //   });
  // }, [editor, value]);
  useEffect(() => {
    return editor.registerUpdateListener(({ editorState }) => {
      editorState.read(() => {
        onChange?.($isRootTextContentEmpty(false));
      });
    });
  }, [editor, onChange]);
  return null;
}

function CodeHighlightPlugin() {
  const [editor] = useLexicalComposerContext();
  useEffect(() => {
    return registerCodeHighlighting(editor);
  }, [editor]);
  return null;
}

function ConverterPlugin({ ref }: { ref: RefObject<MarkdownTextareaRef> }) {
  const [editor] = useLexicalComposerContext();
  useImperativeHandle(
    ref,
    () => ({
      setMarkdown: (content: string) =>
        editor?.update(() => {
          $convertFromMarkdownString(content);
        }),
      getMarkdown: () =>
        editor
          ?.getEditorState()
          ?.read(() => $convertToMarkdownString(TRANSFORMERS)),
    }),
    [editor]
  );
  return <span style={{ display: "none" }} />;
}

export type MarkdownTextareaRef = {
  setMarkdown: (content: string) => void;
  getMarkdown: () => string;
};

export default function MarkdownTextArea({
  isDisabled,
  initialValue,
  onChange,
  footer,
  size = "md",
  instanceId,
  ref,
  converterRef,
  m = "lg",
}: Readonly<{
  isDisabled?: boolean;
  // value is the lexical editor state, not the text/markdown content.
  // Use the functions provided in the ref to decode this to markdown.
  initialValue?: string | null;
  // setValue sets the lexical editor state, not the markdown.
  // Use the functions provided in the ref to encode markdown to this.
  onChange?: (isEmpty: boolean) => void;
  footer?: ReactNode;
  onFocus?: () => void;
  size?: "md" | "sm";
  instanceId: string;
  ref?: RefObject<HTMLDivElement>;
  converterRef: RefObject<MarkdownTextareaRef>;
  m?: 0 | "lg";
}>) {
  const { ref: inputRef, focused } = useFocusWithin();
  const editorConfig: InitialConfigType = {
    editorState: () => $convertFromMarkdownString(initialValue ?? ""),
    onError(error) {
      console.error("Lexical Error:", error);
    },
    nodes: [
      HeadingNode,
      ListNode,
      ListItemNode,
      QuoteNode,
      CodeNode,
      CodeHighlightNode,
      TableNode,
      TableCellNode,
      TableRowNode,
      AutoLinkNode,
      LinkNode,
    ],
    namespace: instanceId,
    theme: {},
  };
  return (
    <LexicalComposer initialConfig={editorConfig}>
      <Card
        withBorder
        className="editor-container"
        mx={0}
        px={0}
        pb={0}
        p={0}
        my={m}
      >
        {focused && (
          <Card.Section inheritPadding withBorder pt="md">
            <ToolbarPlugin size={size} />
          </Card.Section>
        )}
        <div
          className="editor-inner"
          css={css(
            {
              position: "relative",
              outline: "none",
            },
            markdownCss
          )}
          ref={ref}
        >
          <RichTextPlugin
            contentEditable={
              <ContentEditable
                aria-placeholder="enter comment"
                disabled={isDisabled}
                placeholder={
                  <div
                    css={css({
                      position: "absolute",
                      top: 0,
                      pointerEvents: "none",
                    })}
                  >
                    <p>
                      <i
                        css={css({
                          color: "var(--mantine-color-gray-5)",
                          paddingLeft: "var(--mantine-spacing-md)",
                          fontSize: "var(--mantine-font-size-sm)",
                          lineHeight: "var(--mantine-line-height-sm)",
                        })}
                      >
                        Enter a comment here
                      </i>
                    </p>
                  </div>
                }
                className="editor-input"
                ref={inputRef}
                css={css({
                  outline: "none",
                  margin: "var(--mantine-spacing-xs)",
                  // minHeight:
                  //   "calc(2rem + 1rem * var(--mantine-line-height-sm))",
                })}
              />
            }
            ErrorBoundary={ErrorBoundary}
          />
          {/* <AutoFocusPlugin /> */}
          <ListPlugin />
          <LinkPlugin />
          <MyOnChangePlugin onChange={onChange} />
          <MarkdownShortcutPlugin transformers={TRANSFORMERS} />
          <CodeHighlightPlugin />
          <ConverterPlugin ref={converterRef} />
        </div>
        {footer && (
          <Card.Section inheritPadding withBorder>
            {footer}
          </Card.Section>
        )}
      </Card>
    </LexicalComposer>
  );
}
